






















import Vue from 'vue'
import Component from 'vue-class-component';
import Card from '@/components/shared/Card.vue'

@Component({
    components: {
        Card
    },
    computed: {
        avatar() {
            return this.$data.username ? '/img/avatar/' + this.$data.username : '/img/truongba.jpg';
        }
    }
})
export default class Login extends Vue{
    message: string = 'Enter your username and password to continue';
    username: string = '';
    password: string = '';
    snacking: boolean = true;
    loading: boolean = false;

    async login() {
        this.loading = true;
        let res = await fetch(`/api/user/login.php?username=${this.username}&password=${this.password}`, {
            credentials: 'include'
        }).then(res => res.json() as Promise<boolean>);
        if (res == true) {
            this.message = 'Welcome ' + this.username;
            this.snacking = true;
            this.$router.push('/')
        } else {
            this.message = 'Incorrect username or password';
            this.snacking = true;
        }
        this.loading = false;
    }
}
